<style>
.time-entries{
	width: 100%;
}
.list-group-item{
	width: 90%;
	height: 170px;
	margin:20px auto;
	background-color: #fff;
	border: 2px solid #ccc;
	border-radius: 10px;
	position: relative;
	margin-bottom: 40px;
}
.user-details{
	float: left;
	width: 170px;
	height: 170px;
	background-color: #eee;
	border-radius: 11px 0 0 11px;
	text-align: center;
}
.avatar{
	width: 110px;
	height: 110px;
	padding: 10px;
	border-radius: 100%;
}
.time-block{
	padding: 20px;

	text-align: center;
	float: left;
}
.total-time{
	font-size: 2em;
	padding: 20px;
	margin-left: 40px;
	margin-right: 60px;
}
.calendar-time{
	font-size: 1.2em;
	padding: 10px;
}
.comment-section{
	padding: 20px;
	padding-top: 40px;
}

.deletBtn{
	position: absolute;
	right: 7px;
	top: 5px;
	width: 1.2em;
	height: 1em;
	padding: 10px;
	text-align: center;
	border-radius: 5px;
	background-color: #fff;
	-webkit-transition:  0.4s;
	-moz-transition:  0.4s;
	-o-transition:  0.4s;
	transition:  0.4s;
}

.deletBtn:hover{
  cursor: pointer;
  color: #fff;
  background-color: #F45551;
}
.btn-create{
  width: 300px;
	padding: 10px 80px;
	text-align: center;
	border-radius: 5px;
  box-sizing:border-box;
  -moz-box-sizing:border-box; /* Firefox */
  -webkit-box-sizing:border-box;
	background-color: #4CB0F9;
	color: #fff;
	-webkit-transition:  0.4s;
	-moz-transition:  0.4s;
	-o-transition:  0.4s;
	transition:  0.4s;
	font-size: 1.5em;
	cursor: pointer;
  margin-left: 8.66%;
  margin-top: 20px;
}
.btn-create:hover {
	background-color: #1B9AF7;
}
.no-task{
  width: 80%;
  margin:20px auto;
  padding: 20px;
  background-color: #F27A7A;
  border-radius: 5px;
}

@media screen and (max-width: 620px) {  
  .no-task{
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
    margin:10px 0;
  }
  .btn-create{
    margin: 0;
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
  }


  .list-group-item{
    width: 100%;
    height: 100px;
    margin-bottom: 10px;
  }
  .user-details{
    float: left;
    width: 60px;
    height: 100%;
    background-color: #eee;
    border-radius: 11px 0 0 11px;
    text-align: center;
  }
  .avatar{
    width: 40px;
    height: 40px;
    margin: 5px auto;
    border-radius: 100%;
  }
  .time-block{
    padding: 0;
    text-align: center;
    float: left;
  }
  .total-time{
    font-size: 1em;
    padding: 0;
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 30%;
  }
  .calendar-time{
    font-size: .5em;
    padding: 10px;
  }
  .comment-section{
    font-size: 12px;
    padding-top: 3%;
  }
}
</style>


<template>
	<div>

  	<link href="../../static/dist/css/font-awesome.min.css" rel="stylesheet">

		<router-view></router-view>
		
    <p v-if="(!timeEntries.length)&($route.path === '/time-entries')" class="no-task"><strong></strong></p>
    <div 
      v-if="$route.path !== '/time-entries/log-time'" 
      v-link="'/time-entries/log-time'" 
      class="btn-create">
      
    </div>
		<div class="time-entries">
			<article class="list-group-item" v-for="timeEntry in timeEntries" track-by="$index">
				<div class="user-details">
					<img alt="" :src="timeEntry.user.image" class="avatar"/>
					<p class="text-center">
						<strong>
							{{timeEntry.user.name}}
						</strong>
					</p>
				</div>
				<div class="time-block">
					<h3 class="total-time">
						<i class="fa fa-clock-o"></i>
						{{timeEntry.totalTime}}
					</h3>
					<p class="calendar-time">
						<i class="fa fa-calendar"></i>
						{{timeEntry.date}}
					</p>
				</div>

				<div class="comment-section">
					<p>{{timeEntry.comment}}</p>
				</div>
				<span 
					class="deletBtn"
					class="btn btn-xs btn-danger delete-button" 
					@click="deleteTimeEntry(timeEntry)">
					X
				</span>
			</article>
		</section>
	</div>
</template>

<script>
export default{
  data () {
    // 
    return {
      timeEntries: getTimeEntries()
    }
  },
  methods: {
    deleteTimeEntry (timeEntry) {
      // 
      let index = this.timeEntries.indexOf(timeEntry)
      this.timeEntries.splice(index, 1)
      var timeE = this.timeEntries.concat()
      saveTimeEntries(timeE)
    }
  },
  events: {
    updateTimeEntries () {
      this.timeEntries = getTimeEntries()
    }
  }
  // ready: function () {
  //   this.$watch(this.timeEntries, function () {
  //     getTimeEntries()
  //   })
  // }
}

// function deleteTimeEntry (timeEntry) {
//   // 
//   let index = this.timeEntries.indexOf(timeEntry)
//   if (window.confirm('')) {
//     this.timeEntries.splice(index, 1)
//     this.$dispatch('deleteTime', timeEntry)
//   }
// }

function getTimeEntries () {
  // window.alert('JSON.stringify(window.localStorage)')
  if (window.localStorage['timeEntries']) {
    var listString = JSON.stringify(window.localStorage.getItem('timeEntries'))
    listString = listString.substring(0, listString.length - 1)
    listString = listString.substring(1, listString.length)
    listString = listString.replace(/\\/g, '')
    // listString.replace(/"/g, '')
    var listArr = listString.split('time-entry-fenggefu')
    // listArr.splice(1, 1)
    console.log(listString)
    for (var i = 0; i < listArr.length; i++) {
      // window.alert(listArr[i])
      // listArr[i] = '"' + listArr[i] + '"'
      listArr[i] = JSON.parse(listArr[i])
    }
    // listArr[1] = JSON.parse(listArr[1])
    // console.log(listArr[0])
    return listArr
  } else {
    return []
  }
}

function saveTimeEntries (tE) {
  for (var i = 0; i < tE.length; i++) {
    // window.alert(listArr[i])
    // listArr[i] = '"' + listArr[i] + '"'
    tE[i] = JSON.stringify(tE[i])
  }
  window.localStorage['timeEntries'] = tE.join('time-entry-fenggefu')
}

// window.onload = function () {
  // console.log('JSON.stringify(window.localStorage)')
  // var listString = JSON.stringify(window.localStorage.getItem('timeEntries'))
  // var listArr = listString.split('time-entry-fenggefu')
  // listArr.splice(1, 1)
  // window.alert(listArr[1])
  // window.localStorage.clear()
//   window.alert('JSON.stringify(window.localStorage)')
  // window.alert(window.localStorage['timeEntries'])
  // var listString = JSON.stringify(window.localStorage.getItem('timeEntries'))
  // window.alert(listString)
  // window.alert((getTimeEntries()[0].charAt(getTimeEntries()[0].length - 3)))
  // window.alert((getTimeEntries()[0].substr(2)))
// }

</script>
	


