<style>
.todoForm{
  margin-top: 20px;
  text-align: center;
}
.formgroup{
  display: inline-flex;
}
.todoInput{
  padding:1em;
  border: 2px solid #555;
  width: 24em;
  border-radius: 3px;
  font-size: 1.1em;
  opacity: .8;
  -webkit-transition:  0.4s;
  -moz-transition:  0.4s;
  -o-transition:  0.4s;
  transition:  0.4s; 
}
.todoBtn{
  margin: 10px;
  color:#fff;
  padding: 1.28em;
  padding-top: 1.2em;
  border:0;
  width: 6em;
  border-radius: 5px;
  background-color: #4CB0F9;
  /*background: #1B9AF7;*/
  font-size: 1em;
  -webkit-transition:  0.4s;
  -moz-transition:  0.4s;
  -o-transition:  0.4s;
  transition:  0.4s; 
  cursor: pointer;
}
.todoBtn:hover, .todoBtn:focus, .todoBtn:active, .todoBtn.active, .todoBtn.is-active{
  background-color: #1B9AF7;
  border-color: #1B9AF7;
  color: #FFF; 
  cursor: pointer;
}

#todoUl{
  -webkit-transition:  0.4s;
  -moz-transition:  0.4s;
  -o-transition:  0.4s;
  transition:  0.4s; 
}

.todoItems{
  width: 70%;
  margin: 10px auto;
  position: relative;
  white-space:normal;
  background-color: #fff;
  -webkit-transition:  0.2s;
  -moz-transition:  0.2s;
  -o-transition:  0.2s;
  transition:  0.2s; 
  overflow: hidden;
  /*height: 0;*/
}

.todoItems p{
  padding:20px;
  line-height: 2em;
  word-wrap: break-word;
  text-align: left;
}

.todoItems .delete{
  position: absolute;
  right: 5px;
  top: 5px;
  width: 1.1em;
  height: 1.1em;
  border-radius: 5px;
  padding: 5px;
  background-color: #fff;
  -webkit-transition:  0.4s;
  -moz-transition:  0.4s;
  -o-transition:  0.4s;
  transition:  0.4s;
}
.todoItems .delete:hover{
  cursor: pointer;
  color: #fff;
  background-color: #F45551;
}
@media screen and (max-width: 620px) {  
  .formgroup{
    display: block;
  } 
  .todoInput{
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
  }
  .todoBtn{
    margin:10px 0;
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
  }
  .todoItems{
    width: 100%;
  }
}

</style>

<template>
  <div>
    <form class="todoForm">
      <div class="formgroup">
        <input 
          id="todoInput"
          class="todoInput"
          type="text"
          v-model="todoItem"
          placeholder=""
        />
        <div @click="savaItem(todoItem)" class="todoBtn"></div>
      </div>
      <ul id="todoUl">
        <li class="todoItems" v-for="todoItem in todoList" track-by="$index">
          <p>
            {{todoItem}}
          </p>
          <span class="delete" @click="deletTodoItem($index)">x</span>
        </li>
      </ul>
    </form>
  </div>
</template>
<script>
export default {
  data () {
    return {
      todoList: getLocalStorage('myTodoList')
    }
  },
  methods: {
    savaItem (todoItem) {
      if (window.localStorage) {
        if (!window.localStorage['myTodoList']) {
          window.localStorage['myTodoList'] = []
        }
        this.todoList.push(todoItem)
        window.localStorage.setItem('myTodoList', this.todoList)
        todoItem = ''
        document.getElementById('todoInput').value = ''
        // this.$route.router.go({path: '/todo-list'})
      } else {
        window.alert('')
      }
    },
    deletTodoItem (index) {
      this.todoList.splice(index, 1)
      window.localStorage.setItem('myTodoList', this.todoList)
    }
  }
  // ready: function () {
  //   this.$watch('todoList', function (todoList) {
  //     window.localStorage.setItem('myTodoList', todoList)
  //   })
  // }
}

function getLocalStorage (myTodoList) {
  if (window.localStorage['myTodoList']) {
    var listString = JSON.stringify(window.localStorage.getItem('myTodoList'))
    return listString.replace(/"/g, '').split(',')
  } else {
    return []
  }
}

// window.onload = function () {
//   // window.localStorage.clear()
// //   window.alert('JSON.stringify(window.localStorage)')
//   // window.alert(window.localStorage['timeEntries'])
//   window.alert(window.localStorage.getItem('timeEntries'))
//   window.alert(typeof (JSON.stringify(window.localStorage.getItem('timeEntries'))))
// }

</script>


